import React from 'react';

import poster from 'assets/images/movie-cover.png';

import {
    BackgroundImage,
    CollectionFooterContainer,
    CollectionItemContainer,
    NameContainer,
    PriceContainer,
    TypeContainer
} from './collection-item.styles';

const CollectionItem = ({item, addItem}) => {
    const {name, typeFilm, price, imageUrl} = item;

    return (
        <CollectionItemContainer>
            <BackgroundImage className='image' imageUrl={poster}/>
            <CollectionFooterContainer>
                <NameContainer>{name}</NameContainer>
                <TypeContainer>{typeFilm}</TypeContainer>
                <PriceContainer>{price}</PriceContainer>
            </CollectionFooterContainer>
        </CollectionItemContainer>
    );
};


export default CollectionItem;